<template>
  <div class="iq-top-navbar">
    <nav class="nav navbar navbar-expand-lg navbar-light iq-navbar p-lg-0">
      <div class="container-fluid navbar-inner">
        <div class="d-flex align-items-center gap-3" v-if="role == 'admin' || role == 'reviewer' || role == 'auditor'">
          <router-link :to="{ name: 'social.book' }"
            class="d-flex align-items-center gap-2 iq-header-logo d-none d-md-flex">
            <img src="@/assets/images/logo.png" class="img-fluid rounded-circle me-3" alt="user" loading="lazy" />
          </router-link>
          <router-link class="sidebar-toggle" @click="toggleSidebar" data-toggle="sidebar" data-active="true" to="#">
            <div class="icon material-symbols-outlined iq-burger-menu">
              menu
            </div>
          </router-link>
        </div>

        <div class="d-block" v-else>
          <router-link :to="{ name: 'social.book' }" class="d-flex align-items-center gap-2 iq-header-logo">
            <img src="@/assets/images/logo.png" class="img-fluid rounded-circle me-3" alt="user" loading="lazy" />
          </router-link>
        </div>

        <a class="d-lg-none d-flex" href="javascript:void(0);" data-bs-toggle="offcanvas"
          data-bs-target="#offcanvasRight" aria-controls="offcanvasRight">
          <div class="icon material-symbols-outlined iq-burger-menu">menu</div>
        </a>
        <div class="offcanvas offcanvas-end iq-profile-menu-responsive" tabindex="-1" id="offcanvasRight"
          aria-labelledby="offcanvasRightLabel">
          <div class="offcanvas-header">
            <button type="button" class="btn-close text-reset" data-bs-dismiss="offcanvas" aria-label="Close"></button>
          </div>
          <div class="offcanvas-body pt-0">
            <ul class="navbar-nav ms-auto navbar-list">
              <li class="nav-item">
                <router-link :to="{ name: 'social.book' }" class="d-flex align-items-center">
                  <i class="material-symbols-outlined">home</i>
                  <span data-bs-dismiss="offcanvas" class="mobile-text d-lg-none ms-3">الصفحة الرئيسية</span>
                </router-link>
              </li>
              <li class="nav-item">
                <router-link class="d-flex align-items-center" :to="{ path: `/profile` }">
                  <i class="material-symbols-outlined">person</i>
                  <span data-bs-dismiss="offcanvas" class="mobile-text d-lg-none ms-3">انجازاتي</span>
                </router-link>
              </li>
               <li class="nav-item" v-if="userStatus==2">
                <router-link class="d-flex align-items-center" :to="{ name: 'user.update-info' }">
                  <i class="material-symbols-outlined">edit</i>
                  <span data-bs-dismiss="offcanvas" class="mobile-text d-lg-none ms-3">توثيق الحساب</span>
                </router-link>
              </li>
              <li class="nav-item">
                <a href="javascript:void(0);" class="d-flex align-items-center" @click="store.dispatch('logout')">
                  <i class="material-symbols-outlined">logout</i>
                  <span class="mobile-text d-lg-none ms-3">تسجيل خروج</span>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </nav>
  </div>
</template>
<script>
import { useStore } from "vuex";
import { computed } from "vue";
import userService from "../../../../api/userServices";
import UserInfo from "../../../../services/userInfo.service";
export default {
  name: "DefaultHeader",
  async created(){
    await this.getUserStatus();
  },
  props: {
    image: {
      type: String,
      default: require("@/assets/images/logo.png"),
    },
    role: {
      type: String,
    },
  },
  data() {
    return {
      show: false,
      userStatus:""

    };
  },
  methods: {
       async getUserStatus(){
      const id = UserInfo.getId();
      const res = await userService.getUser(id);
      this.userStatus = res.is_active;
      console.log(this.userStatus)
    },
  },
  setup() {
    const store = useStore();
    const sidebarType = computed(() => store.getters["setting/sidebar_type"]);
    const toggleSidebar = () => {
      // Code Here
      if (sidebarType.value.includes("sidebar-mini")) {
        store.dispatch(
          "setting/sidebar_type",
          sidebarType.value.filter((item) => item !== "sidebar-mini")
        );
      } else {
        store.dispatch("setting/sidebar_type", [
          ...sidebarType.value,
          "sidebar-mini",
        ]);
      }
    };
    return {
      store,
      sidebarType,
      toggleSidebar,
    };
  },
};
</script>
